export default {
  props: {
    // æå¨èå´ï¼åä½æ¯px, ä¾å¦ï¼{x: 4, y: 2, z: 10}
    range: {
      type: Object,
      default: () => { return { z: 8 }; },
    },
    start: {
      type: Boolean,
      required: true,
    },
    shiftPercent: {
      type: Number,
      default: 0.1, // ç§»å¨rangeä¸­åå§å¼çç¾åæ¯
    },
    perspectiveStyle: {
      type: Object,
      default: () => {
        return {
          perspective: '300px',
          perspectiveOrigin: 'center center'
        };
      }
    }
  },
  data() {
    return {
      timer: null,
      currentRange: null,
      move: null,
      position: null,
      shiftNumber: null,
      // åé¡ºæ¶éæ¹åæå¨
      isClockwise: true,
    };
  },
  mounted() {
    // å¦æè¦æ§è¡ z è½´å¨ç»éè¦è®¾ç½®ç¶çº§ï¼ä»èä¿®æ¹å­çº§éè§ææï¼å¦å Z è½´æ²¡æææ
    if (this.range.z > 0) {
      const parentEl = this.$el.parentNode;
      Object.keys(this.perspectiveStyle).forEach((key) => {
        parentEl.style[key] = this.perspectiveStyle[key];
      });
    }
  },
  methods: {
    /**
     * è·åéè¦æä½ççé¡¹ å æ¯æ¬¡éè¦æå¨çé
     */
    getPositionAndShiftNumber() {
      const result = {
        position: [],
        shiftNumber: {}
      };
      const range = this.currentRange;
      let val;
      for (const key in range) {
        val = range[key];
        if (val > 0) {
          result.position.push(key);
          result.shiftNumber[key] = this.shiftPercent * val;
        }
      }
      return result;
    },
    /**
     * åå§åæå¨
     */
    initJitter() {
      // æstartåæfalseï¼ æ¹ä¾¿ä¸æ¬¡ç¹å»
      this.$emit('update:start', false);
      // æ¸é¤ä¸æ¬¡å¨ç»
      this.clearAnimate();
      // è®¾ç½®currentRange, å¡«åthis.range ä¸­æ²¡æçé¡¹
      this.currentRange = Object.assign({}, { x: 0, y: 0, z: 0 }, this.range);
      // è·åéè¦æä½ççé¡¹ å æ¯æ¬¡éè¦æå¨çé
      const { position, shiftNumber } = this.getPositionAndShiftNumber();
      this.position = position;
      this.shiftNumber = shiftNumber;
      // åå§ move èµ·å§ç¹æ¯0
      this.move = { x: 0, y: 0, z: 0 };
      // åå§æ¶ æ¯é¡ºæ¶é
      this.isClockwise = true;
      // æ§è¡å¨ç»
      this.timer = window.requestAnimationFrame(this.continueJitter);
    },
    /**
     * æ´æ°åç´ æå¨çä½ç§»
     */
    refreshMove(direction) {
      const shiftNumber = this.shiftNumber;
      this.position.forEach((item) => {
        this.move[item] += direction * shiftNumber[item];
      });
    },
    /**
     * è·åç§»å¨æ¶çç»å¯¹å¼
     */
    getAbsMove() {
      const absMove = {};
      for (const m in this.move) {
        absMove[m] = Math.abs(this.move[m]);
      }
      return absMove;
    },
    // æç»­æå¨
    continueJitter() {
      this.refreshMove(this.isClockwise ? -1 : 1);
      // ç»å¯¹å¼
      const absMove = this.getAbsMove();
      const currentRange = this.currentRange;
      let changeDirection = false;
      for (let i = 0, l = this.position.length, p; i < l; i += 1) {
        p = this.position[i];
        // å¤æ­æ¯å¦å°è¾¾ä¸´çå¼ï¼å°è¾¾å åºè¯¥åæ¹åæ§è¡å¨ç»
        if (currentRange[p] <= absMove[p]) {
          // ç­æ¯ä¾ç¼©å
          this.currentRange[p] -= this.shiftNumber[p];
          // å¤æ­å¦æå·²ç»æ ååæå¨ï¼å°±è®©æå¨åæ­¢, åªè¦æä¸ä¸ªå¼è¾¾å°äº0ï¼ææé½ä¼è¾¾å°
          if (this.currentRange[p] <= 0) {
            // åæ­¢å¨èµ·å§ç¹ä¸
            this.jitterView({ x: 0, y: 0, z: 0 });
            // æ¸é¤å¨ç»
            this.clearAnimate();
            return;
          }
          // æ´æ°moveä¸ºä¸´çç¹
          this.move[p] = this.isClockwise ? -this.currentRange[p] : this.currentRange[p];
          // æ¹åæå¨æ¹å
          changeDirection = true;
        }
      }
      if (changeDirection) {
        // æå¨æ¹ååå
        this.isClockwise = !this.isClockwise;
      }
      // æ´æ°åç´ ä½ç½®
      this.jitterView(this.move);
      // ç»§ç»­æ§è¡å¨ç»
      this.timer = window.requestAnimationFrame(this.continueJitter);
    },
    /**
     * ä¿®æ¹åç´ ä½ç½®
     * @param x
     * @param y
     * @param z
     */
    jitterView({ x = 0, y = 0, z = 0 }) {
      this.$el.style.transform = `translate3d(${x}px, ${y}px, ${z}px)`;
    },
    /**
     * æ¸é¤å¨ç»
     */
    clearAnimate() {
      if (this.timer) {
        window.cancelAnimationFrame(this.timer);
      }
    }
  },
  destroyed() {
    this.clearAnimate();
  },
  watch: {
    start(newVal) {
      // å¼å§æå¨
      if (newVal) {
        this.initJitter();
      }
    }
  }
};



